
import Vue from 'vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Icon from 'vux/src/components/icon/index.vue'


let wx = Vue.wechat
// wx.config({
//     debug: true, // ,apialertpclogpc
//     appId: '', // 
//     timestamp: , // 
//     nonceStr: '', // 
//     signature: '',// 1
//     jsApiList: [] // JSJS2
// })

wx.ready(() => {

    // configreadyconfigconfigreadyready
})

export default {
  components: {
    XInput,
    XButton,
    Box,
    Card,
    Group,
    Cell,
    Icon
  },
  methods: {
    picker () {
      let self = this
      wx.chooseImage({
        count: 1, // 9
        sizeType: ['original', 'compressed'], // 
        sourceType: ['album', 'camera'], // 
        success: function (res) {
          self.ticketUrl = res.localIds // IDlocalIdimgsrc
        }
      })
    }
  },
  data () {
    return {
      title: '',
      ticketUrl: ''
    }
  }
}



